import '../scss/main.scss'

// ----------nav bar controller


let prev = document.querySelector('.home');
let prevContent = document.querySelector('.content-home');

document.querySelector('.navbar').addEventListener('click', (e) => {
  e.preventDefault()
  const parent = e.target.parentNode
  const parentContent = document.querySelector(`.content-${parent.classList[1]}`)
  prev.classList.remove('active')
  prevContent.classList.remove('active')
  parent.classList.add('active')
  parentContent.classList.add('active')
  prev = parent
  prevContent = parentContent
})


// portfolio controller

let prevArr = document.querySelectorAll('.all')
let prevfilActive = document.querySelector('.fil-active')

document.querySelector(`.filter-navbar`).addEventListener('click', (e) => {
  if (e.target.classList[0] === 'atag') {
    const parentCls = e.target.parentNode.id;
    const fillActive = e.target;
    const nodeArr = document.querySelectorAll(`.${parentCls}`)
    prevfilActive.classList.remove('fil-active')
    prevArr.forEach(el => {
      el.classList.remove('active')
    })
    fillActive.classList.add('fil-active')
    nodeArr.forEach(el => {
      el.classList.add('active')
    })
    prevArr = nodeArr
    prevfilActive = fillActive
  }

})